<template>
    <div class="" style="height:100%;">
        <div class="login-header">
          <div class="top-back" @click="goBack" v-show="false">
            <span class="mint-button-icon"><i class="mintui mintui-back"></i></span>
          </div>
    	  <div style="float:right">
    	  <span class="" @click="showManger">{{rightName}}</span>
    	  </div>
          <div class="center">
            购物车({{cartList.length}})
          </div>
        </div>
        <div class="cart-contain" v-if="cartList.length!=0">
            <div class="" v-show="isShowManger"><button class="btn-delete" type="button" @click="clearCart">删除</button></div>
            <ul class="list-infinite cancel-link"  >
              <li v-for="(item,index) in cartList">

                <div class="financial-box-2" >
                <div> <div class="check" :class="{active:item.checked,mintui:item.checked}" @click="select(item)"></div></div>
                  <div class="img-contain">
                      <img :src="img1">
                  </div>
                  <div class="text-contain">

                    <div>{{ item.goodsName }}</div>
                    <span class="badge">{{item.spec}}</span>
                    <div class="qx"><span class="qx-text">下单时间{{item.orderDate }}</span></div>
                    <div class="right btn-component"> <button type="button" @click="upShopCartUpdSub(index)">-</button><span>{{item.qty}}</span><button type="button" @click="upShopCartUpdAdd(index)" >+</button></div>
                  </div>
                </div>

              </li>
            </ul>
        <div class="balance-cart normal-box"> <div>
            <div class="check t-left" :class="{active:balanceChecked ,mintui:balanceChecked}" @click="selectAll"></div>全选</div>

            <div class="t-right " style="margin-right:6px;"> 合计 &nbsp;: &nbsp;<span>11111</span></div>
            <div class="t-right flew-glow-inherit"><button type="button" @click="goOrder">结算</button></div>
        </div>
        </div>
        <div class="jqqd" v-if="cartList.length==0">
         购物车是空的
        </div>
    </div>
</template>
<script type="text/javascript">
import {
  MessageBox
} from 'mint-ui'
  export default{
      data(){
        return {
            isShowManger:false,
            balanceChecked:false,
            rightName:"管理",
            cartList:[],
            selectList:[]
        }
      },
      created:function(){
          this.upShopCartQry();
      },
      computed:{
          img1:function(){
              return require('assets/logo.png');
          }
      },
      methods:{
          upShopCartQry:function(){
              var pars={
                  limit:global.pageSize,
                  offset:"0"
              }
              axiosUtil.upShopCartQry(pars).then(data=>{
                  console.log("--------购物车")
                  console.log(data)
                  var data=data.data;
                  data.rows.forEach((item,index)=>{
                      item["checked"]=false;
                  })
                  this.cartList=data.rows;
              })
          },
          upShopCartUpdSub:function(index){
              this.cartList[index].qty=this.cartList[index].qty-1;
              this.upShopCartUpd(index);
          },
          upShopCartUpdAdd:function(index){
              this.cartList[index].qty=this.cartList[index].qty+1;
              this.upShopCartUpd(index);
          },
          upShopCartUpd:function(index){
              var pars={
                  orderSno:this.cartList[index].orderSno,
                  qty:this.cartList[index].qty,
              };
              axiosUtil.upShopCartUpd(pars).then(data=>{
                  console.log("购物车修改");
                  var data=data.data;
                   console.log(data);
                  if(data.errorcode==0){
                      console.log("购物车修改成功");
                  }else{
                      MessageBox("提示",data.errormsg);
                  }
              })
          },
          clearCart:function(){
              this.selectList.forEach((item,index)=>{
                  var pars={
                      orderSno:item.orderSno,
                  }
                  axiosUtil.upShopCartDel(pars).then(data=>{
                      console.log("购物车删除");
                      var data=data.data;
                      console.log(data);
                      if(data.errorcode==0){
                          this.upShopCartQry();
                      }else{
                          MessageBox("提示",data.errormsg);
                      }
                  })
              })

          },
          showManger:function(){
              this.isShowManger=!this.isShowManger;
              this.rightName= this.isShowManger?"完成":"管理";
          },
          goBack:function(){
              this.$router.go(-1);
          },
          selectAll:function(){
              this.balanceChecked=!this.balanceChecked;
              if(this.balanceChecked){
                  this.cartList.forEach((item,index)=>{
                      item["checked"]=true;
                  });
                  this.selectList=this.cartList;
              }else{
                  this.cartList.forEach((item,index)=>{
                      item["checked"]=false;
                  });
                  this.selectList=[];
              }

          },
          select:function(item){
              item.checked=!item.checked;
              if(item.checked){
                  this.selectList.push(item);
              }else{
                  for(let i=0;i<this.selectList.length;i++){
                      if(data.orderSno===item.orderSno){
                         this.selectList.splice(i,1);
                         break;
                      }
                  }
              }
          },
          goOrder:function(){
              this.$router.push("order");
          },
      }

  }
</script>
<style media="screen" scoped>
.login-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  height: 45px;
  line-height: 45px;
  font-size: 19px;
  background: #ef4f4f;
  color: #ffffff;
  text-align: center;
}

.top-back {
  max-width: 85px;
  float: left;
}

.financial-box-2{
    display: flex;
}
.financial-box-2 .img-contain{
    width: 26vw;
}
.financial-box-2 .img-contain img{
    width: 100%;
}

.financial-box-2  .text-contain{
    padding: 12px;
}
.cancel-link  a{
  text-decoration: none;
  color: #111;
}
.list-infinite{
    width: 100%;
}
.cancel-link  .check{
    margin-top: 10vw;
}
.check{
    width: 22px;
    height: 22px;
    line-height: 22px;
    margin-right: 6px;
    background: rgba(0,0,0,0);
    border-radius: 22px;
    border: 2px solid #999;

}
.check.active{
    border: none;
    color: #EF734F;
    font-size: 24px;
}
.check.active:before{
    content: "\E609";
}
.btn-delete{
    float: right;
    border: none;
    border: 1px solid #f40;
    background: inherit;
    padding: 6px 24px;
    color: #f40;
    border-radius: 16px;
    font-size: 12px;
    margin-top: 12px;
}
.balance-cart .check{
    margin-top: 12px;
    margin-left: 12px;
}
.balance-cart{
    position: fixed;
        bottom: 44px;
        background: #fff;
        border-top: 1px solid #ccc;
        height: 44px;
        line-height: 44px;
}
.balance-cart button{
    height: 44px;
    width: 100px;
    text-align: center;
    line-height: 44px;
    flex-grow: inherit;
    border:none;
    color: #fff;
background: #f40;
}
.cart-contain{
    height: 100%;
}
</style>



